import React from "react";
import classnames from "classnames";
import Switch from "react-switch";
import { withStyles } from '@material-ui/core/styles';
import moment from "moment";
import { Snackbar } from '@material-ui/core';
import { green } from '@material-ui/core/colors';
import { red } from '@material-ui/core/colors';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import ContentLoader, { List } from "react-content-loader";
import Select from "react-select";

import {
  getCurrentDate,
  getTomorrowDate,
  get2DayAfterDate,
  getYesterdayDate,
  get1YesterdayDate,

} from "../../variables/Date/Date";
import {
  getChampionshipByCountry,
  getCountryByDate,
  getMatchsByChampionship,
} from "../../Services/getApiData/getApiDataService";
import {
  addPronostic,
  getPronostics,
  getPronosticsOld,
  deletePronostics

} from "../../Services/Pronostics/PronosticsService";
import nopronostic from "../../assets/img/logo192black.png";
import soccer from "../../assets/img/soccer.jpg";
// reactstrap components
import {
  TabContent,
  TabPane,
  Table,
  Container,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Nav,
  NavItem,
  NavLink,
  Badge,
  Button,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Modal,
  Form,
  Input,
  Tooltip,
} from "reactstrap";

const customStyles = {
  option: (provided, state) => ({
    ...provided,
    color: state.isSelected ? "black" : "blue",
  }),
};


const optionItemsPrediction = [
  { value: 'Home Win', label: 'Home Win' },
  { value: 'Away Win', label: 'Away Win' },
  { value: 'Double Chance Home', label: 'Double Chance Home' },
  { value: 'Double Chance Away', label: 'Double Chance Away' },
  { value: 'Two Teams Goals', label: 'Two Teams Goals' },
  { value: "Two Teams Don't Goals", label: "Two Teams Don't Goals" },
  { value: 'Over 0.5', label: 'Over 0.5' },
  { value: 'Under 0.5', label: 'Under 0.5' },
  { value: 'Over 1.5', label: 'Over 1.5' },
  { value: 'Under 1.5', label: 'Under 1.5' },
  { value: 'Over 2.5', label: 'Over 2.5' },
  { value: 'Under 2.5', label: 'Under 2.5' },
  { value: 'Over 3.5', label: 'Over 3.5' },
  { value: 'Under 3.5', label: 'Under 3.5' },

  
]

moment.locale('fr', {
  months : 'janvier_février_mars_avril_mai_juin_juillet_août_septembre_octobre_novembre_décembre'.split('_'),
  weekdays : 'dimanche_lundi_mardi_mercredi_jeudi_vendredi_samedi'.split('_'),
  relativeTime : {
      future : 'dans %s',
      past : 'il y a %s',
      s : 'quelques secondes',
      m : 'une minute',
      mm : '%d minutes',
      h : 'une heure',
      hh : '%d heures',
      d : 'un jour',
      dd : '%d jours',
      M : 'un mois',
      MM : '%d mois',
      y : 'un an',
      yy : '%d ans'
  }
});

